import { useEffect, useState } from 'react';

import emoji from '../../emojies/locked-with-key.png'

import Button from '../../components/Button';
import Placeholder from '../../components/Placeholder';

import getCountryCode  from './getCountryCode';

import './Auth.css';

function Auth() {
  const [loaded, setLoaded] = useState(false)
  const [number, setNumber] = useState('')

  useEffect(() => {
    async function fetchData() {
      let countryCode = await getCountryCode()
      setNumber(countryCode)
    }
    fetchData();
  })
  return (
    <div className="Auth__in">
        <Placeholder icon={<div className='Auth_AuthBlock_image'>
                <img src={emoji} width={96} height={96} />
            </div>}>
            <h3>Войти или создать профиль</h3>
            <div className='Auth__NumberInput'>
                <label>Номер телефона</label>
                <div className='Auth__NumberInput_input'>
                    <input className='Auth__NumberInput_el' disabled={!loaded} value={number} type='tel' pattern="[+]{1}[0-9]{11,14}" maxLength={12}></input>
                </div>
                <Button size='xl'>Продолжить</Button>
            </div>
        </Placeholder>
    </div>
  );
}

export default Auth;