import React, {useEffect, useState} from "react";
import "./components.css";

let Button = (props) => {
    let [typeClasses, setTypeClasses] = useState('Button');
    let [sizeClasses, setSizeClasses] = useState('Button__m');

    useEffect(()=>{
        switch (props.type) {
            case "secondary":
                setTypeClasses("Button__secondary");
                break
            default:
                setTypeClasses("Button");
                break
        }
    }, [props.type])

    useEffect(()=>{
        switch (props.size) {
            case "m":
                setSizeClasses("Button__m");
                break
            case "l":
                setSizeClasses("Button__l");
                break
            case "xl":
                setSizeClasses("Button__xl");
                break
            default:
                setSizeClasses("Button__m");
                break
        }
    }, [props.size])


    return(
        <div {...props} onClick={props.onClick} className={typeClasses+" "+sizeClasses}>
            <div className="Button__content">
                {props.children}
            </div>
        </div>
    );
}
export default Button;