let codes = {
    "KZ": "+77",
    "RU": "+79",
    "US": "+1",
    "UK": "+44",
    "BR": "+375",
    "KG": "+996",
    "LV": "+371",
    "MD": "+373",
    "UA": "+380"
}

export default async function() {
    let req = await fetch('https://ipinfo.io/json?token=d0fe18c0145640')
    req = await req.json()
    console.log(`GET from https://ipinfo.io/json: ${req}`)
    
    return codes[req.country] || ""
}